.google-login-button {
  display: flex;
  align-items: center;
  background-color: #4285f4;
  color: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  padding: 5px 12px; /* 텍스트 주변에 적절한 여백 */
}

.google-login-button:hover {
  background-color: #357ae8;
}

.bc-logo {
  background-color: #ffffff;
  border-radius: 5px;
  padding: 4px; /* 로고 주변의 여백을 줄여서 깔끔하게 */
  margin-right: 10px; /* 로고와 텍스트 사이의 간격 */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px; /* 하얀 배경이 잘 보이도록 크기 조정 */
}

.google-logo {
  width: 18px;
  height: 18px;
}

.login-text {
  color: white; /* 텍스트를 흰색으로 변경 */
  font-size: 16px;
}
